@import url(https://fonts.googleapis.com/css2?family=Kaisei+Tokumin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@500&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --primary-white : #fff;
    --primary-blue : #4C43D4;
    --primary-dark : rgba(255,255,255,0);
    --primary-orange : #f9ab00;
}

body{
    font-family: 'Kaisei Tokumin', serif;
}


.navbar-custom {
    background-color: rgba(32,44,148,255)
    ;
}

.navbar-light .navbar-toggler {
    color: white;
    border-color: white;
}

.logo{
    height: 3.125rem;
    width: 4.375rem;
}

nav a.nav-link {
    font-weight: 200;
    letter-spacing: 0.0625rem;
    cursor: pointer;
}

nav ul li {
    
    text-transform: uppercase;
    
}
.navbar-nav{
    margin-left: auto;
}

.navbar-nav > li > a {
    margin-left: 0.625rem;
    margin-right: 0.625rem;
    color: azure !important;
    border-bottom: 0.1875rem solid rgba(255,255,255,0);
    border-bottom: 0.1875rem solid var(--primary-dark) ;
}

.navbar-nav > li > a:focus {
    border-bottom: 0.1875rem solid #4C43D4;
    border-bottom: 0.1875rem solid var(--primary-blue);
}

.navbar-nav > li > a:hover {
    border-bottom: 0.1875rem solid #4C43D4;
    border-bottom: 0.1875rem solid var(--primary-blue);
    color: sandybrown !important;
}

/********************Header*************************/

.bg-image {
    
    position: relative;
    background: url(/portfolio/static/media/bg.f0ed7a0e.jpg) no-repeat;
    background-size: cover;
    background-position:center;
    height:90vh;
    overflow: hidden;
    
   

   
}
.bg-text  {

  background-color: rgb(0,0,0) ;
  background-color: rgba(0,0,0, 0.65) ; 
  color: #f9ab00; 
  color: var(--primary-orange) ;
  text-transform: uppercase ;
  font-weight: bold ;
  border: 3px solid #f1f1f1 ;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family:'Cormorant Garamond', serif;
  z-index: 2;
  height: 20vh;
  width: 60vw ; 
  padding: 20px 0 20px 0;
  
  text-align: center;
   justify-content: center;
 

    
}
.typed-text {
    text-transform: capitalize;
    color: white;
    letter-spacing: 1.5px;
}



.tsparticles-canvas-el{
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
}
nav{
    z-index: 2;
}
.about__main {
    background-color: rgba(0, 0, 0, 0.87);
}
img.about__img{
    border-radius: 50%;
    margin-top: 30px;
    margin-bottom: 30px;
    margin-left: 20px;
    margin-right: 20px;
    
    
}

img.about__img:hover{
    border-radius: 10%;
    
}

h1.about__heading{
    margin-top: 30px;
    font-size: 50px;
    color: #f9ab00;
    text-align: left;
}

h1.about__heading:hover{
    letter-spacing: 3px;
    color: red;
}
.right_side {
    margin-left: 30px;
}
.name{
    font-family: 'Times New Roman', Times, serif;
    letter-spacing: 1px;
    
}
h3{
    color: white;
    text-align: start;
   
}
p{
    color: whitesmoke;
}

@media (max-width:576px) {
    h1.about__heading{
        text-align: center;
    }
    img.about__img {
        margin: 30px 0 0 0;
        display: flex;
        justify-content: center;

    }
}
